import { Box, Button, TextField } from "@material-ui/core";
import React from "react";

const Login = () => {
  return (
    <Box
        display='flex'
        alignItems='center'
        justifyContent='center'
    >
        <Box 
        padding={2}
        width='25%'
        height='25%'
        >
            <Box>
                <TextField 
                    label='usename'
                    autoFocus
                    required 
                    fullWidth
                />
            </Box>
            <Box>
                <TextField 
                    label='password' 
                    type='password'
                    required
                    fullWidth
                />
            </Box>
            <Button variant='contained' >Log in</Button>
        </Box>
    </Box>
    
  );
}

export default Login;
